.UserContainer {
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 90%;
}
.UserProfile {
	text-align: center;
	background-color: var(--secondaryBG);
	margin: 1rem auto;
	height: fit-content;
	width: 250px;
	border: var(--Appborder);
	-webkit-box-shadow: 3px 3px 5px 2px rgba(237, 242, 244, 0.3);
	box-shadow: 3px 3px 5px 0px rgba(237, 242, 244, 0.25);
}
.userAvatar {
	height: 150px;
	margin-top: 0.5rem;
}

.userName {
	font-weight: var(--fontSemiBold);
	font-size: 1.5rem;
	color: var(--secondaryFontColour);
	margin: 0.5rem;
}

.userRealName {
	font-weight: var(--fontLight);
	margin: 0.5rem;
}
