@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #2b2d42;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

:root {
	--mainBG: #2b2d42;
	--secondaryBG: #8d99ae;
	--fontColour: #edf2f4;
	--secondaryFontColour: #000;
	--mainRed: #d90429;
	--secondaryRed: #ef233c;
	--Appborder: 1px solid var(--fontColour);
	--fontLight: 300;
	--fontRegular: 400;
	--fontSemiBold: 600;
	--fontBold: 700;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.App {
	height: 99.9vh;
	font-family: 'Montserrat', sans-serif;
	font-weight: 400;
	font-weight: var(--fontRegular);
	position: relative;
	display: grid;
	grid-template-rows: 1fr 10fr 1fr;
	grid-template-areas:
		'header'
		'main'
		'footer';
	background-color: #2b2d42;
	background-color: var(--mainBG);
	color: #edf2f4;
	color: var(--fontColour);
}
.container{
	grid-area: main;
	
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
.ArticleList {
	
	grid-area: main;
	overflow: hidden;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	overflow: scroll;
}

#Footer {
	/* display: none; */
	max-height:100px;
	background-color: #d90429;
	background-color: var(--mainRed);
	grid-area: footer;
	border: 1px solid #edf2f4;
	border: var(--Appborder);
}

#FooterH3 {
	text-align: center;
	margin: 1rem auto;
}

.ArticleCard {
	height: 250px;
	width: 90%;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: flex-start;
	background-color: #8d99ae;
	background-color: var(--secondaryBG);
	border: 1px solid #edf2f4;
	border: var(--Appborder);
	color: #000;
	color: var(--secondaryFontColour);
	margin: 1rem auto;
	box-shadow: 3px 3px 5px 0px rgba(237, 242, 244, 0.25);
}

.articleTitle {
	margin: 1rem 0 0 1rem;
	width: 90%;
}
.articleTopic{
	font-weight: 300;
	font-weight: var(--fontLight);
}
.textOverflowOff {
	word-break: break-word;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	line-height: 16px;
	max-height: 4rem;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
}

.articleAuthor {
	font-weight: 300;
	font-weight: var(--fontLight);
	margin: 1rem 0 0 1rem;
}

.articleBody {
	font-size: 0.9rem;
	font-weight: 400;
	font-weight: var(--fontRegular);
	height: 100px;
	width: 90%;
	margin: 1rem 0 0 1rem;
}
.votes {
	display: flex;
	width: 90%;
	justify-content: space-between;
	margin: 1rem 0 1rem 1rem;
}
.articleButton {
	text-decoration: none;
	padding: 2%;
	color: #edf2f4;
	color: var(--fontColour);
	background-color: #d90429;
	background-color: var(--mainRed);
	border: 1px solid #edf2f4;
	border: var(--Appborder);
}
.ArticleSort{
	padding: 2%;
	color: #edf2f4;
	color: var(--fontColour);
	background-color: #d90429;
	background-color: var(--mainRed);
	border: 1px solid #edf2f4;
	border: var(--Appborder);
}
.ArticleSort > button{
	
	text-decoration: none;
	height: 20px;
	padding:0% 0.5%;
	margin: 0.2rem 1rem;
	color: #edf2f4;
	color: var(--fontColour);
	background-color: #d90429;
	background-color: var(--mainRed);
	border: 1px solid #edf2f4;
	border: var(--Appborder);
}
@media only screen and (min-width: 1120px) {
	.App {
		margin: 0 auto;
		width: 90vw;
	}

	.ArticleList {
		overflow-x: hidden;
		grid-area: main;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
	}
	.ArticleCard {
		height: 250px;
		width: 450px;
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: flex-start;
		background-color: #8d99ae;
		background-color: var(--secondaryBG);
		border: 1px solid #edf2f4;
		border: var(--Appborder);
		color: #000;
		color: var(--secondaryFontColour);
		margin: 1rem auto;
	}

	/* width */
	::-webkit-scrollbar {
		width: 10px;
	}

	/* Track */
	::-webkit-scrollbar-track {
		background: #2b2d42;
		background: var(--mainBG);
	}

	/* Handle */
	::-webkit-scrollbar-thumb {
		background: #d90429;
		background: var(--mainRed);
	}

	/* Handle on hover */
	::-webkit-scrollbar-thumb:hover {
		background: #edf2f4;
		background: var(--fontColour);
	}
}

#Header {
	font-weight: var(--fontBold);
	background-color: var(--mainRed);
	grid-area: header;
	border: var(--Appborder);
	position: relative;
}
#HeaderH1 {
	margin: 1rem 4rem;
	font-size: 2.5rem;
}
#menuIcon {
	position: absolute;
	top: 5%;
	right: 5%;
}

#Nav {
	display: none;
	background-color: var(--mainRed);
}

#navList {
	list-style: none;
	display: flex;
	flex-direction: row;
}

.navLink {
	text-decoration: none;
	color: var(--fontColour);
	font-weight: var(--fontLight);
	margin: 0 0.5rem;
}

.Menu {
	z-index: 10;
	background: url(/news-frontEnd/static/media/menu.6f0a109e.svg);
	top: 8%;
	right: 5%;
	position: absolute;
	height: 254px;
	width: 203px;
}
#menuList {
	font-size: 1.5rem;
	text-align: left;
	list-style: none;
	margin: 3rem auto;
}

#closeMenu {
	position: absolute;
	top: 15px;
	right: 20px;
}

@media only screen and (min-width: 1120px) {
	#Nav {
		display: block;
		position: absolute;
		right: 4%;
		bottom: 5%;
		font-size: 1.5rem;
	}
	#menuIcon {
		display: none;
	}
	.Menu {
		display: none;
	}
}

.singlearticle {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;
	background-color: var(--secondaryBG);
	color: var(--secondaryFontColour);
	margin: 1rem auto;
	max-height: -webkit-fit-content;
	max-height: -moz-fit-content;
	max-height: fit-content;

	width: 90%;
	box-shadow: 3px 3px 5px 0px rgba(237, 242, 244, 0.25);
	border: 4px solid var(--fontColour);
}

.singlearticleHeading {
	margin: 1rem 1rem 0.5rem 1rem;
	font-weight: var(--fontSemiBold);
}

.singlearticleAuthor {
	margin: 0rem 1rem 1rem 1rem;
	font-weight: var(--fontLight);
}

.singlearticleBody {
	margin: 0rem 1rem 1rem 1rem;
}
.sAVotesContainerer {
	width: 100%;
	height: 50px;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
}
.singlearticleVotes {
	font-weight: var(--fontSemiBold);
}

.singlearticleButton {
	width: 75px;
	height: 25px;
	color: var(--fontColour);
	background-color: var(--mainRed);
	border: var(--Appborder);
}
.CommentsContainer {
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-around;
	max-height: -webkit-min-content;
	max-height: min-content;
}
.CommentsContainer .CommentCard:nth-child(1n + 1) {
	border: 4px solid var(--fontColour);
	border-radius: 24px 24px 0px 24px;
}
.CommentsContainer .CommentCard:nth-child(2n + 2) {
	border: 4px solid var(--fontColour);
	border-radius: 24px 24px 24px 0px;
}
#AddCommentForm {
	display: flex;
	flex-direction: row;

	height: 25px;
	margin: 1rem auto;
}
#addCommentBtn {
	margin: 0 1rem;
	width: 100px;
	padding: 5px;
}
#commentInput {
	margin: 0 1rem;
}
.mr1{
	margin-right: 1rem;
}
@media only screen and (min-width: 1120px) {
}

.CommentCard {
	color: var(--secondaryFontColour);
	grid-area: comments;
	background-color: var(--secondaryBG);
	margin: 1rem auto;
	box-shadow: 3px 3px 5px 0px rgba(237, 242, 244, 0.25);
	/* border: 4px solid var(--fontColour);
	border-radius: 24px 24px 0px 24px; */
	width: 80%;
	max-height: -webkit-fit-content;
	max-height: -moz-fit-content;
	max-height: fit-content;
}

.commentAuthor {
	font-weight: var(--fontLight);
	margin: 1rem 1rem;
}

.commentBody {
	padding: 1rem 0;
	margin: 1rem 1rem;
}
.commentsVoteSection {
	display: flex;
	flex-direction: row;
	width: 90%;
	justify-content: space-between;
	margin: 0.2rem 1rem 1rem 1rem;
}
.commentButton {
	text-decoration: none;
	padding: 2%;
	color: var(--fontColour);
	background-color: var(--mainRed);
	border: var(--Appborder);
}

.disabled {
	display: none;
}
@media only screen and (min-width: 1120px) {
	.CommentCard {
		margin: 1rem 0.5rem;
		width: 400px;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
	}
}

.errorContainer {
	margin: 5rem auto;
	width: 90%;
	text-align: center;
}

.errorStatus {
	font-size: 10rem;
}

.errorMsg {
	font-size: 2rem;
}
.gif {
	margin: 2rem;
}

.UserContainer {
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 90%;
}
.UserProfile {
	text-align: center;
	background-color: var(--secondaryBG);
	margin: 1rem auto;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	width: 250px;
	border: var(--Appborder);
	box-shadow: 3px 3px 5px 0px rgba(237, 242, 244, 0.25);
}
.userAvatar {
	height: 150px;
	margin-top: 0.5rem;
}

.userName {
	font-weight: var(--fontSemiBold);
	font-size: 1.5rem;
	color: var(--secondaryFontColour);
	margin: 0.5rem;
}

.userRealName {
	font-weight: var(--fontLight);
	margin: 0.5rem;
}

